.Header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    padding: 15px;
}
.logo img {
    width: 150px;
    margin-left: 60px;
}
.logoimg {
    margin-left: 40px;
}

.navigcia-first {
    list-style: none;
    cursor: pointer;
}
.navigcia-first:hover {
    color: gray;
}
.navigcia-centr {
    list-style: none;
    cursor: pointer;
}
.navigcia-centr:hover {
    color:gray;
}
.navigcia-last {
    list-style: none;
    cursor: pointer;
}
.navigcia-last:hover {
    color: gray;
}

.navigcia li {
    display: inline-block;
    margin-right: 20px;
    font-size: 19px;


}
.nav-and-lang {
    display: flex;
    align-items: center;
    gap: 20px;

}
.langselect {
    margin-right: 30px;
    height: 28px;
    width: 120px;
    font-size: 15px;
    border-radius: 5px; /* Normal border radius */
    background-color: #00B9F8;
    color: white;
}



.langselect option {
    background-color: white;
    color: black;
    border-radius: 0px;
}
