.formlogin {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.login, .password {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-right: 120px;
    margin-left: 120px

}

input[type="text"], input[type="password"] {
    border: none;
    border-radius: 20px;
    padding: 10px;
    text-align: center;
    width: 340px;
    background: #E8E6FF;
}

.helppassword {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    font-size: 10px;
    gap: 135px;
}

.Show {
    margin-right: 20px;
    cursor: pointer;
    color: white;
    display: flex;
    align-items: center;
}

.forget {
    text-decoration: none;
    cursor: pointer;
    color: white;
    display: flex;
    align-items: center;
}
:hover.forget {
    color: #4d4d4d;
}

.buttonsubmit button {
    border: none;
    border-radius: 20px;
    padding: 10px 40px;
    background: linear-gradient(90deg, #7B6CEA 9.12%, #AE61FF 42.93%, #7359EA 94.89%);
    color: white;
    cursor: pointer;
}

.buttonsubmit button:hover {
    background-color: #888888;
}
.label-for-showPassword {
    display: inline-block;
    width: 30px; /* Задайте желаемую ширину и высоту для создания круглой формы */
    height: 30px;
    border-radius: 50%; /* Делаем круглой */
    background: linear-gradient(90deg, #7B6CEA 9.12%, #AE61FF 42.93%, #7359EA 94.89%);
    color: white; /* Цвет текста */
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

input[type="checkbox"] {
    display: none;
}

input[type="checkbox"] + label {
    position: relative;
    padding-left: 20px; /* Размер круглой области */
    cursor: pointer;
    padding-top: 2px;
}


input[type="checkbox"] + label::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 15px; /* Диаметр круга */
    height: 15px; /* Диаметр круга */
    border-radius: 50%; /* Превращаем в круг */
    background: linear-gradient(90deg, #7B6CEA 9.12%, #AE61FF 42.93%, #7359EA 94.89%);
}

/* Отображаем галочку при отмеченном чекбоксе */
input[type="checkbox"]:checked + label::before {
    content: "\2713"; /* Юникод-символ галочки */
    text-align: center;
    font-size: 14px; /* Размер символа */
    line-height: 15px; /* Выравнивание символа по вертикали */
    color: #3D405F; /* Цвет символа */
}


