.Info {
    display: flex;
    justify-content: space-around;
}
.Info-button {
    display: flex;
    justify-content: space-between;
}
.Info-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 50%; /* Each block occupies 50% */
    padding: 0 100px; /* Add padding for spacing */

}

.Info-right {
    flex: 1;
    display: flex;
    align-items: center;
    width: 50%; /* Each block occupies 50% */
    padding: 0 20px; /* Add padding for spacing */
}

.Info-text {
    color: white;
    font-size: 45px;
    font-weight: 700;

}
.registration {
    margin-top: 25px ;
}

.come {
    width: 125px;
    height: 30px;
    top: 208px;
    left: 20px;
    padding: 5px;
    border-radius: 41px;
    gap: 10px;
    color: #00B9F8;
    cursor: pointer;
    border: none;
    margin-right: 25px;
}

.Become-a-member {
    width: 200px;
    height: 30px;
    top: 208px;
    left: 132px;
    padding: 5px;
    border-radius: 41px;
    gap: 10px;
    cursor: pointer;
    border: none;
    background: linear-gradient(90.09deg, #7359EA -1.33%, #AE61FF 41.19%, #7359EA 99.93%);
    color: white;
}

.WelcomePage {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Info {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Info-button {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background-color: #3C3C56;
    color: white;
}

.Untitled img {
    width: 150%;
}
